import React from 'react';

function Home() {
  return (
    <div style={{ display: 'flex', justifyContent: 'center' }}>
      <img
        src="assets/images/LogoApp.png"
        alt="assets/images/LogoApp.png"
      />
    </div>
  );
}

export default Home;



